import React, { FC } from 'react';
import { RouteComponentProps, Link } from 'react-router-dom';
import { toggleNavMenu } from 'store/NavMenu/actions';
import { useSelector, useDispatch } from 'react-redux';
import cls from 'classnames';
import './style.scss';
import { ReduxStore } from 'store/types';
import { MenuList, MenuItem } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles(theme => ({
  menuList: {
    color: '#000',
  },
}));

const Navigation: FC<RouteComponentProps> = props => {
  const dispatch = useDispatch();
  const store = useSelector((state: ReduxStore) => state.navMenu);
  const Classes = useStyles();
  const itemClick = (route: string) => () => {
    dispatch(toggleNavMenu());
    props.history.push(route);
  };
  return (
    <div
      className={cls('Navigation', {
        'Navigation--hidden': !store.isOpen,
      })}
    >
      <h1>this is navigation component</h1>
      <MenuList className={Classes.menuList}>
        <MenuItem onClick={itemClick('/')}>Map</MenuItem>
        <MenuItem onClick={itemClick('/bookmarks')}>Bookmarks</MenuItem>
        <MenuItem onClick={itemClick('/settings')}>Settings</MenuItem>
      </MenuList>
    </div>
  );
};

export default Navigation;
