import React, { FC, useEffect } from 'react';
import { useSelector, useDispatch, shallowEqual } from 'react-redux';
import * as actions from '../store/InitConfig/actions';
import { ReduxStore } from '../store/types';
import splash from './image.png';
import './style.scss';
// @ts-ignore
const InitWrapper: FC = props => {
  const dispatch = useDispatch();

  const initConfig = useSelector((state: ReduxStore) => state.initConfig);
  const shops = useSelector(
    (state: ReduxStore) => state.shops.content,
    shallowEqual
  );
  const { children = null } = props;
  useEffect(() => {
    dispatch(actions.initFetch());
  }, [dispatch]);
  return !shops.length ? (
    <div
      style={{
        width: '100vw',
        height: '100vh',
        backgroundImage: `url(${splash})`,
        backgroundPosition: 'cover',
      }}
    />
  ) : (
    children
  );
};

export default InitWrapper;
