import React, { FC, useContext } from "react";
import { ShopState, AppLatLng } from "store/types";
import { Box, IconButton, Paper, Typography, makeStyles } from "@material-ui/core";
import clsx from "clsx";
import { toggleBookmark } from "store/Bookmark/actions";
import { useDispatch } from "react-redux";
import { __RouterContext } from 'react-router';
import { distance } from "helpers";

const useStyles = makeStyles(theme => ({
  searchResultCard: {
    height: '160px',
    position: 'relative',
    color: 'rgba(0, 0, 0, 0.87)',
    borderRightWidth: 10,
    borderRightStyle: 'solid',
  },
  yellowCard: {
    borderRightColor: '#eeee00',
  },
  blueCard: {
    borderRightColor: '#0000dd',
  },
  searchResultImage: {
    height: '75px',
    width: '75px',
  },
  bookmarkButton: {
    boxShadow: '0 1px 3px #bbb',
    position: 'absolute',
    top: '30px',
    right: '40px',
    zIndex: 600,
  },
}));

function shopDist(shop: ShopState, refLatLng: AppLatLng) {
  const dist = distance(shop.latlng, refLatLng);
  return dist < 1 ? `${Math.round(dist * 1000)} m` : `${dist.toFixed(2)} km`;
}

type Props = {
  className: string;
  shop: ShopState;
  bookmarked: boolean;
  refLatLng: AppLatLng | null;
};

const ShopInfoCard: FC<Props> = props => {
  const {className, shop, bookmarked, refLatLng} = props;

  const classes = useStyles();
  const { history } = useContext(__RouterContext);
  const dispatch = useDispatch();

  return <Box className={className} position='relative' p='10px'>
    <IconButton
      size='small'
      className={classes.bookmarkButton}
      onClick={() => dispatch(toggleBookmark(shop.id))}
    >
      {bookmarked ?
        <i className='icon-good' style={{ fontSize: '12px' }} /> :
        <i className='icon-bookmark' style={{ fontSize: '12px' }} />
      }
    </IconButton>
    <Box
      component={Paper}
      display='flex'
      flexDirection='column'
      maxHeight='100%'
      px={2}
      onClick={() => history.push(`/detail/${shop.id}`)}
      style={{ cursor: 'pointer' }}
      className={clsx(
        classes.searchResultCard,
        classes.yellowCard
      )}
    >
      <Box display='flex' flexDirection='row' mt={2}>
        <Box mr={1}>
          <img
            src='https://via.placeholder.com/150'
            alt={shop.name}
            className={classes.searchResultImage}
          />
        </Box>
        <Box display='flex' flexDirection='column' flex={1}>
          <Box display='flex' flexDirection='row' flex={1}>
            <Box
              fontWeight={600}
              textAlign='left'
              fontSize='14px'
              pr='30px'
              flex={1}
            >
              <Typography>{shop.name}</Typography>
            </Box>
          </Box>
          <Typography variant='caption' color='textSecondary'>
            <Box
              display='flex'
              flexDirection='row'
              mb={1}
              fontSize='13px'
            >
              <Box textAlign='left' flex={1}>
                {shop.industryTypeName}
                {refLatLng ? `  ${shopDist(shop, refLatLng)}` : null}
              </Box>
              <Box textAlign='right' color='green'>
                
              </Box>
            </Box>
          </Typography>
        </Box>
      </Box>
      <Box
        mt={1}
        fontSize='13px'
        lineHeight='20px'
        maxHeight='40px'
        textAlign='left'
        overflow='hidden'
      >
        Placeholder
      </Box>
    </Box>
  </Box>;
}

export default ShopInfoCard;
